.modalfranchize__backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  z-index: 1;
  background: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modalfranchize__backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modalfranchize {
  width: 280px;
  background: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.backdropfranchize.is-hidden .modalfranchize {
  transform: translate(-50%, -50%)scale(.9);
}

