* {
    box-sizing: border-box;
}



.modalfranchize__backdrop {
    position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 1;
        visibility: visible;
        transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
        background: rgba(0, 0, 0, 0.5);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1;
    
        &.is-hidden {
            visibility: hidden;
            opacity: 0;
            pointer-events: none;
        }
}





.modalfranchize {
    width: 280px;
    // height: 674px;
    position: relative;
    padding: 25px;
    background: #FCFCFC;
    border: 2px solid #F8D24E;
    border-radius: 10px;
    // margin-left: auto;
    // margin-right: auto;

    
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    .backdropfranchize.is-hidden & {
            transform: translate(-50%, -50%) scale(0.9);
        }
    @media screen and (min-width:768px) {
     width: 568px;
     height: 668px;
     padding: 32px 45px;
    } 
} 
