.modalVideo__backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    visibility: visible;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    &.is-hidden {
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
    }
}

.modalVideo {
    width: calc(100% - 20px);
    background: #FFFFFF;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
    margin: 0 auto;
    position: relative;
    padding: 28px;
    text-align: center;
    border: 2px solid #F8D24E;
    border-radius: 10px;

@media screen and (min-width: 480px) {
        width: 480px;
        padding: 32px 60px;
    }

    @media screen and (min-width: 768px) {
            width: 568px;
            
        }
    
       
    label {
        display: inline-block;
        margin-bottom: 4px;
        color: var(--contact-color);
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.01em;
    }
    &__close {
        position: absolute;
        cursor: pointer;
        top: 17px;
        right: 17px;
        background: transparent;
        border: none;
        padding: 0;
    }
    &__title {
        font-family: 'Seymour One', sans-serif;
        font-size: 20px;
        line-height: calc(28/20);
        color: $title-color;
        margin-bottom: 32px;
    }
    &__lorem {
        margin-top: 32px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        color: #131313;
        line-height: 22px;
        text-align: left;
    }
}
