// footer
@media screen and (max-width: 768px) {

.footer {
    background-color: #84A178;
    

}

.footer__container {
    margin-left: auto;
    margin-right: auto;
    max-width: 480px;
    padding-left: 20px;
    padding-right: 20px;
}


.logo__primary {
    font-family: 'Montserrat', sans-serif ;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;

    color: #FCFCFC;
    margin-bottom: 16px;
    width: 232px;
}

.logo__secondary {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 41px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    width: 200px;

    color: #FCFCFC;
    margin-bottom: 60px;
}



.social__list {
    display: flex;
    margin-bottom: 20px;
    margin-left: auto;
    width: 142px;

}

.social__item:not(:last-child) {
    margin-right: 20px;
}

.link__text {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.8;

    color: #131313;
    margin-left: auto;

}

.link__number {
    font-family: 'Montserrat',sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.8;

    color: #FCFCFC;
    margin-bottom: 40px;
    margin-left: 5px;

}

.link {
    margin-bottom: 40px;
    display: flex;
    height: 22px;
}




.bottom-text {
    text-align: center;
    width: 160px;
    padding-bottom: 10px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.8;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    
    color: #7A7A7A;
}

.line {
    border-top: 1px solid #E1E1E1;
    margin-bottom: 20px;

}

/////
}

